.filter-title{font-size:1.75rem;text-transform:uppercase;font-weight:500;padding:0 0 2rem 1rem}.btn-clear,.btn-apply{width:12rem;height:4rem;border-radius:12px;font-size:large;font-weight:500;color:#323759;text-transform:uppercase;border:none}.btn-clear{background-color:#f5f5f5;margin-right:80px}.btn-clear:focus,.btn-clear:hover{color:#323759;background-color:#f5f5f5}.btn-apply{background-color:#FDD84C}.btn-apply:focus,.btn-apply:hover{color:#323759;background-color:#FDD84C}.ant-slider{width:360px}.ant-slider-track{background-color:#FDD84C;border-radius:2px}.ant-slider-handle-dragging.ant-slider-handle-dragging.ant-slider-handle-dragging{border-color:#7367f0;box-shadow:0 0 0 5px #1890ff1f}.ant-slider-track:focus{background-color:red;border-radius:2px}.ant-slider-handle{border:2px solid #7367f0;border-radius:2px}.ant-slider-handle:focus{border:2px solid #7367f0}.ant-slider:hover .ant-slider-track{background-color:#FDD84C}.ant-slider:hover .ant-slider-handle:not(.ant-tooltip-open){border-color:#7367f0 !important}.ant-slider-handle.ant-tooltip-open{border-color:#7367f0 !important}.btn-container{position:fixed;bottom:2rem}
